import React, {Component} from 'react';

class TeamLogo extends Component {
    render() {
        var url = urlStart + this.props.teamId;
        if (this.props.small) {
            url = url + urlEndSmall;
        } else if (this.props.large) {
            url = url + urlEndLarge;
        }else {
            url = url + urlEndMed;
        }

        return (
            <>
                {this.props.teamId ? (

                <img src={url} alt="team logo"/>
                ) : ""}
            </>
        );
    }
}

var urlStart = "https://secure.espn.com/combiner/i?img=/i/teamlogos/ncaa/500/";
var urlEndSmall= ".png?w=25&h=25";
var urlEndMed= ".png?w=50&h=50";
var urlEndLarge= ".png?w=100&h=100";

export default TeamLogo;