import React, {Component} from 'react';
import TeamLogo from './TeamLogo';

class Team extends Component {
    render() {
        var team = this.props.team;
        return (
            <div
                onClick={() => {this.props.onClick(team, this.props.round)}}
                onMouseEnter={() => {this.props.onHover(team)}}>
                {this.props.small ? (
                    <TeamLogo teamId={team.team_id} small></TeamLogo>
                ) : (
                    <TeamLogo teamId={team.team_id}></TeamLogo>
                )}
            </div>
        );
    }
}

export default Team;